import { Flex } from '@chakra-ui/react'
import Icon from './Icon'

export default function CheckBox(props: {
  checked: boolean
  width?: string
  height?: string
  borderRadius?: string
  onClick: () => void
  cursor?: string
}) {
  const { checked, width, height, borderRadius, onClick, cursor = 'pointer' } = props
  return (
    <Flex
      align="center"
      justifyContent="center"
      background="checked_bg"
      border="1px solid"
      borderColor="border"
      cursor={cursor}
      width={width || '20px'}
      height={height || '20px'}
      borderRadius={borderRadius || '4px'}
      onClick={onClick}
      sx={{
        svg: {
          fill: 'primary !important'
        }
      }}
    >
      {checked ? <Icon xlinkHref="#icon-icon_check" /> : null}
    </Flex>
  )
}
